@use '~/src/ui/style/vars' as *;
@use '~/src/ui/style/functions' as *;

.loaderBox {
  display: flex;
  justify-content: center;
  align-items: center;
}

.commentBox {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: auto;

  .inputBox {
    width: 100%;
  }

  .input {
    border: none;
    border-radius: 0;
    padding: 0;
  }

  .postButton {
    min-width: fit-content;
    color: $blue;

    &:hover {
      color: darken($blue, 10%)
    }

    &:disabled {
      background-color: $white;
      border-color: $white;
      color: $gray;
    }
  }
}

.phoneModal {
  .commentsViewBox {
    margin: 24px 0;
  }

  .content {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 24px 16px;
    overflow: hidden auto;
    @include hide-scrollbar;
  }
}

.modal {
  width: rem(1080px);
  max-width: 95%;
  height: rem(680px);
  padding: 0;

  .box {
    display: flex;
    width: 100%;
    height: 100%;

    .image {
      width: 55%;
      height: 100%;
    }

    .infoSection {
      display: flex;
      flex-direction: column;
      width: 45%;

      & > * {
        padding: rem(20px) rem(32px);

        &:not(:last-child) {
          border-bottom: solid 1px $gray;
        }
      }

      .userBox {
        display: flex;
        align-items: center;
        gap: rem(16px);

        .name {
          font-size: 16px;
          font-weight: 500;
          color: $black;
        }

        .closeButton {
          outline: none;
          border: none;
          background-color: transparent;
          cursor: pointer;
          padding: 0;
          margin-left: auto;
          font-size: rem(32px);
          color: $dark-gray;
          display: flex;
        }
      }

      .commentsViewBox {
        flex: 1;
      }

      .count {
        margin-left: 8px;
        font-size: 16px;
        font-weight: 600;
      }

      .like {
        display: flex;
        align-items: center;
        padding: 0;

        i {
          font-size: 24px;
          color: $gray;
        }

        .liked {
          color: $red;
        }
      }
    }
  }
}
